.service-box p {
    margin-bottom: 0;
}

.galeria-box {
    display: block;
    position: relative;
    margin: 0 auto;
    max-width: 650px;
}

.galeria-box .galeria-box-caption {
    display: block;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    color: $border-color;
    opacity: 0;
    background: $color_9;
    -webkit-transition: all .35s;
    -moz-transition: all .35s;
    transition: all .35s;
}

.galeria-box .galeria-box-caption .galeria-box-caption-content {
    position: absolute;
    top: 50%;
    width: 100%;
    text-align: center;
    transform: translateY(-50%);
}

.galeria-box .galeria-box-caption .galeria-box-caption-content .project-category,
.galeria-box .galeria-box-caption .galeria-box-caption-content .project-name {
    padding: 0 15px;
    font-family: $Roboto;
}

.galeria-box .galeria-box-caption .galeria-box-caption-content .project-category {
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
}

.galeria-box .galeria-box-caption .galeria-box-caption-content .project-name {
    font-size: 18px;
}

.galeria-box:hover .galeria-box-caption {
    opacity: 1;
}

@media(min-width:768px) {
    .galeria-box .galeria-box-caption .galeria-box-caption-content .project-category {
        font-size: 16px;
    }

    .galeria-box .galeria-box-caption .galeria-box-caption-content .project-name {
        font-size: 22px;
    }
}
