.hidden {
    display: none;
}

hr {
    max-width: 50px;
    border-color: $main-color;
    border-width: 3px;
}

hr.light {
    border-color: $border-color;
}

a {
    color: $main-color;
    -webkit-transition: all .35s;
    -moz-transition: all .35s;
    transition: all .35s;
}

a:hover,
a:focus {
    color: $lighter-color;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $Roboto;
}

p {
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 1.5;
}

.bg-primary {
    background-color: $accent-color;
}

.bg-dark {
    color: $border-color;
    background-color: $font-color;
}

.text-faded {
    color: $text-faded;
}

section {
    padding: 100px 0;
}

aside {
    padding: 50px 0;
}

.no-padding {
    padding: 0;
}


.call-to-action h2 {
    margin: 0 auto 20px;
}

.text-primary {
    color: $main-color;
}

.icon-primary {
    color: $main-color;
}

.no-gutter > [class*=col-] {
    padding-right: 0;
    padding-left: 0;
}

.btn-default {
    border-color: $border-color;
    color: $font-color;
    background-color: $border-color;
    -webkit-transition: all .35s;
    -moz-transition: all .35s;
    transition: all .35s;
}

.btn-default:hover,
.btn-default:focus,
.btn-default.focus,
.btn-default:active,
.btn-default.active,
.open > .dropdown-toggle.btn-default {
    border-color: $color_2;
    color: $font-color;
    background-color: $color;
}

.btn-default:active,
.btn-default.active,
.open > .dropdown-toggle.btn-default {
    background-image: none;
}

.btn-default.disabled,
.btn-default[disabled],
fieldset[disabled] .btn-default,
.btn-default.disabled:hover,
.btn-default[disabled]:hover,
fieldset[disabled] .btn-default:hover,
.btn-default.disabled:focus,
.btn-default[disabled]:focus,
fieldset[disabled] .btn-default:focus,
.btn-default.disabled.focus,
.btn-default[disabled].focus,
fieldset[disabled] .btn-default.focus,
.btn-default.disabled:active,
.btn-default[disabled]:active,
fieldset[disabled] .btn-default:active,
.btn-default.disabled.active,
.btn-default[disabled].active,
fieldset[disabled] .btn-default.active {
    border-color: $border-color;
    background-color: $border-color;
}

.btn-default .badge {
    color: $border-color;
    background-color: $font-color;
}

.btn-primary {
    border-color: $main-color;
    color: $border-color;
    background-color: $main-color;
    -webkit-transition: all .35s;
    -moz-transition: all .35s;
    transition: all .35s;
}

.btn-primary:hover,
.btn-primary:focus,
.btn-primary.focus,
.btn-primary:active,
.btn-primary.active,
.open > .dropdown-toggle.btn-primary {
    border-color: $lighter-color;
    color: $border-color;
    background-color: $lighter-color;
}

.btn-primary:active,
.btn-primary.active,
.open > .dropdown-toggle.btn-primary {
    background-image: none;
}

.btn-primary.disabled,
.btn-primary[disabled],
fieldset[disabled] .btn-primary,
.btn-primary.disabled:hover,
.btn-primary[disabled]:hover,
fieldset[disabled] .btn-primary:hover,
.btn-primary.disabled:focus,
.btn-primary[disabled]:focus,
fieldset[disabled] .btn-primary:focus,
.btn-primary.disabled.focus,
.btn-primary[disabled].focus,
fieldset[disabled] .btn-primary.focus,
.btn-primary.disabled:active,
.btn-primary[disabled]:active,
fieldset[disabled] .btn-primary:active,
.btn-primary.disabled.active,
.btn-primary[disabled].active,
fieldset[disabled] .btn-primary.active {
    border-color: $lighter-color;
    background-color: $lighter-color;
}

.btn-primary .badge {
    color: $main-color;
    background-color: $border-color;
}

.btn {
    border: 0;
    border-radius: 300px;
    text-transform: uppercase;
    font-family: $Roboto;
    font-weight: 700;
}

.btn-xl {
    padding: 15px 30px;
}

::-moz-selection {
    text-shadow: none;
    color: $border-color;
    background: $font-color;
}

::selection {
    text-shadow: none;
    color: $border-color;
    background: $font-color;
}

img::selection {
    color: $border-color;
    background: 0 0;
}

img::-moz-selection {
    color: $border-color;
    background: 0 0;
}
